@layer base {
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  html {
    scroll-behavior: smooth;
    height: 100%;
  }
  body {
    background-color: #050505;
    background-image: radial-gradient(circle at 20% 50%, rgba(120, 40, 200, 0.05) 0%, transparent 50%),
                      radial-gradient(circle at 80% 80%, rgba(200, 40, 120, 0.05) 0%, transparent 50%);
    color: #ffffff;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    min-height: 100vh;
    width: 100%;
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  @keyframes gradient-shift {
    0%, 100% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
  }
  @keyframes float {
    0%, 100% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-10px);
    }
  }
  @keyframes pulse-glow {
    0%, 100% {
      filter: drop-shadow(0 0 15px rgba(29, 185, 84, 0.5));
    }
    50% {
      filter: drop-shadow(0 0 25px rgba(29, 185, 84, 0.9));
    }
  }
  @keyframes gradient-x {
    0%, 100% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
  }
  .gradient-animate {
    background-size: 200% 200%;
    animation: gradient-shift 15s ease infinite;
  }
  .animate-float {
    animation: float 3s ease-in-out infinite;
  }
  .animate-gradient {
    background-size: 200% auto;
    animation: gradient-x 3s linear infinite;
  }
  ::-webkit-scrollbar {
    width: 10px;
  }
  ::-webkit-scrollbar-track {
    background: #1a1a1a;
  }
  ::-webkit-scrollbar-thumb {
    background: linear-gradient(to bottom, #9333ea, #ec4899);
